        },
        {
          question:
            "在铁路线路两侧`20`米以内或者铁路防护林地内放牧的，铁路职工有权制止。",
          options: ["20", "50", "100"],
        },
        {
          question: "不能认定为工伤或者视同工伤的情形为`以上都有`",
          options: [
            "故意犯罪的",
            "醉酒或者吸毒的",
            "自残或者自杀的",
            "以上都有",
          ],
        },
        {
          question: "有源应答器和无源应答器中的“源”是指`信号源`",
          options: ["电源", "信号源", "都不是"],
        },
        {
          question: "不能消除焦虑情绪的方法是`接着焦虑`",
          options: ["恢复自信", "反省解", "自我催眠", "接着焦虑"],
        },
        {
          question: "列控系统CTCS等级有`5`级。",
          options: ["2", "3", "5"],
        },
        {
          question: "CTCS-2级列控系统由`列控车载设备`和地面设备组成。",
          options: ["通信设备", "信号设备", "列控车载设备"],
        },
        {
          question: "ATP是中文名为`列车超速防护`。",
          options: ["列控车载设备", "列车超速防护", "列车控制"],
        },
      ],
    };
  },
  created() {
    this.current_index = this.$store.state.single_choice_progress;
    this.update_current_question_info();
  },
  methods: {
    get_pure_question_and_answer(text) {
      let question = text.replace(/`(.*?)`/, "(   )");
      let right_answer = text.match(/`(.*?)`/)[1];
      return { question, right_answer };
    },
    update_current_question_info() {
      let current = this.question_list[this.current_index];
      let { question, right_answer } = this.get_pure_question_and_answer(
        current.question
      );
      this.picked = ""
      this.current_question_info = {};
      this.current_question_info.question = question;
      this.current_question_info.right_answer = right_answer;
      this.current_question_info.options = current.options;
    },
    next_question() {
      console.log("right");
      this.current_index = this.current_index + 1;
      if (this.current_index > this.question_list.length - 1) {
        this.current_index = 0;
      }
      this.update_current_question_info();
      this.$store.commit("set_single_choice_progress", this.current_index);
    },
  },
  watch: {
    picked: function(val) {
      const delay_time = 500;
      if (val == this.current_question_info.right_answer) {
        setTimeout(()=>{
          this.next_question()
        }, delay_time)
      } else {
        setTimeout(()=>{
          this.picked = ""
        }, delay_time)
      }
    },
  }
};
</script>

