<template>
  <div class="center-box">
    <h4>{{ current_question_info.question }}</h4>
    <div class="align">
      <div class="align2">
        <input type="radio" :id="1" :value="true" v-model="picked" />
        <label :for="1">True</label>
      </div>
      <div class="align2">
        <input type="radio" :id="2" :value="false" v-model="picked" />
        <label :for="2">False</label>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "TrueOrFalse",
  props: {},
  data: () => {
    return {
      current_question_info: {},
      picked: "",
      right_list: [
        "严禁班前、班中饮酒；严禁脱岗、串岗、私自替班、换班、班中赌博、擅自脱岗，不得做与工作无关的事情，按规定携带和使用手机。",
        "在站内进行施工、维修、巡检（检查）等作业时，严禁穿越来车线避车。",
        "高于36V 电压设备带电作业时，必须使用绝缘性能良好的工具，穿绝缘鞋（室内应站在绝缘垫上）；不得同时接触导电和接地部分。",
        "作业过程中，不准将手探入尖轨与基本轨间。",
        "电务系统所有在列车速度160 Km /h 及以上区段作业人员必须经过段一级教育、培训和考试，熟知有关人身作业安全防范措施。",
        "夜间、隧道作业，必须保证足够的照明灯具、充足的照明时间和亮度。",
        "在接触网下作业时，从业人员必须与其保持2米的安全距离。",
        "作业人员必须服从防护人员指挥，按要求进行作业，及时下道、按规定线路行走，避车时列队面向列车方向站立。",
        "冬季室外作业时，所戴防寒帽应有耳孔；冰雪凝冻天气，须采取防滑、防摔伤措施，不准用围巾、衣帽等裹遮耳目。",
        "在施工、检修、故障处理等各项作业中，违章使用封连线是电务系统基本规章“红线”。",
        "设备故障或其他非正常情况需上道作业时，必须按规定申请本线封锁、邻线限速，经准许后方可进行。",
        "高速铁路：严禁天窗点外上道作业。路局公布的“点外作业项目”可在天窗点外实施，必须严格执行点外作业管理的相关规定，应急处置按相关规定办理。",
        "上道作业的人员、工器具进出高速铁路必须进行清点。",
        "作业过程中，不准将手探入尖轨与基本轨间；需要扳动道岔时，应确认尖轨与基本轨之间无作业人员和工具。",
        "企业文化的外显部分是指企业的文化设施、文化教育、技术培训和文娱、联谊活动等",
        "我们的企业使命是—“服务经济社会、服务大众需求、服务企业员工”。",
        "应答器分为有源应答器和无源应答器。",
        "采用GSM-R通信系统实现车地间双向无线数据传输，提供车地之间双向安全数据传输通道。",
        "CTC应具备分散自律控制和非常站控两种模式",
        "CTC侧重于行车控制，TDCS侧重于调度指挥。",
      ],
      wrong_list: [
        "站内线路上施工、维修、巡检等有关作业必须设驻站联系人和现场检修人员。",
        "当站内正线或与正线相邻设备故障处理恢复后，相关人员应从距正线线路外侧5米以上安全距离外返回车站。",
        "作业人员在站内作业时，必须携带无线电台、手机，并保证电量充足。",
        "禁止从车辆下部或车钩处通过。在停留列车、车辆前部或尾部通过时，应与其保持3m以上距离。",
        "在电气化区段工作的职工，可以登上机车车辆顶部或翻越车顶通过线路。",
        "扛抬笨重物品时，每人负重一般不得超过100Kg。",
        "职工因醉酒导致伤亡的可以视情况认定为工伤或者视同工伤。",
        "故障处理人员到达车站后应立即在车站《行车设备检查登记簿》内登记到达时间。向车站值班员（助理值班员或信号员）了解设备故障情况及查看故障现象，需要操纵设备时，可不经车站值班员同意，并确认有关线路上没有进行接发列车和调车作业后方可进行。",
        "故障处理人员确认故障现象后，登记故障设备停用时，可不登记影响范围等内容，如危及行车安全需立即抢修时，应按有关要求办理。",
        "在区间、站内或段管线发生铁路交通事故时，现场作业人员应立即报告电务段值班调度、必要时可以向车站值班员报告。",
        "新设备开通使用前，电务段应组织设计、施工部门和产品供应商，对维修人员进行技术培训，培训不合格仍准许上岗作业。",
        "作业人员身体和携带物件（如长杆、导线、工具等〉与接触网设备带电部分，必须保持5米以上的距离，与回流线有2米以上的距离。",
        "不准将电流互感器一次线圈开路，以免产生高电压击穿设备和危及人身安全。",
        "转辙机内部作业时可以戴手套。",
        "对损毁、移动铁路信号装置及其他行车设施或者在铁路线路上放置障碍物的，铁路职工有权制止，但不能扭送公安机关处理。",
        "铁路工作人员班前可以饮酒、严禁班中饮酒，在执行职务时，不得与别人闲谈或做与本职无关的工作。工作前，要充分的休息，保证工作的精力充沛，思想集中。",
        "允许速度：列车运行过程中允许达到的最高速度；",
        "高职生和铁路员工的身份没有很大区别",
      ],
    };
  },
  created() {
    //this.current_index = this.$store.state.single_choice_progress;
    this.update_current_question_info();
  },
  methods: {
    update_current_question_info() {
      this.current_question_info = {};
      this.picked = "";
      if (Math.random() > 0.5) {
        this.current_question_info.question = this.right_list[
          Math.floor(Math.random() * this.right_list.length)
        ];
        this.current_question_info.answer = true;
      } else {
        this.current_question_info.question = this.wrong_list[
          Math.floor(Math.random() * this.wrong_list.length)
        ];
        this.current_question_info.answer = false;
      }
    },
    next_question() {
      let old_question = this.current_question_info.question
      while (this.current_question_info.question == old_question) {
        this.update_current_question_info();
      }
    },
  },
  watch: {
    picked: function (val) {
      const delay_time = 500;
      console.log(val);
      if (val === this.current_question_info.answer) {
        setTimeout(() => {
          this.next_question();
        }, delay_time);
      } else {
        setTimeout(() => {
          this.picked = "";
        }, delay_time);
      }
    },
  },
};
</script>

