  methods: {
    similar(s, t, f) {
      s = s.replace(/[^\u4E00-\u9FA5]/g, "");
      t = t.replace(/[^\u4E00-\u9FA5]/g, "");
      if (!s || !t) {
        return 0;
      }
      var l = s.length > t.length ? s.length : t.length;
      var n = s.length;
      var m = t.length;
      var d = [];
      f = f || 3;
      var min = function (a, b, c) {
        return a < b ? (a < c ? a : c) : b < c ? b : c;
      };
      var i, j, si, tj, cost;
      if (n === 0) return m;
      if (m === 0) return n;
      for (i = 0; i <= n; i++) {
        d[i] = [];
        d[i][0] = i;
      }
      for (j = 0; j <= m; j++) {
        d[0][j] = j;
      }
      for (i = 1; i <= n; i++) {
        si = s.charAt(i - 1);
        for (j = 1; j <= m; j++) {
          tj = t.charAt(j - 1);
          if (si === tj) {
            cost = 0;
          } else {
            cost = 1;
          }
          d[i][j] = min(
            d[i - 1][j] + 1,
            d[i][j - 1] + 1,
            d[i - 1][j - 1] + cost
          );
        }
      }
      let res = 1 - d[n][m] / l;
      console.log(res.toFixed(f))
      return res.toFixed(f);
    },
    get_similarity_from_two_strings(a, b) {
      a = a.replace(/[^\u4E00-\u9FA5]/g, "");
      b = b.replace(/[^\u4E00-\u9FA5]/g, "");
      let set_a = new Set(a);
      let set_b = new Set(b);
      let set_in_common = set_a || set_b;
      console.log(set_in_common.size / set_b.size)
      return set_in_common.size / set_b.size;
    },
    update_current_question_info() {
      this.current_question_info.question = this.question_list[
        this.current_index
      ].question;
      this.current_question_info.right_answer = this.question_list[
        this.current_index
      ].answer;
    },
    next_question() {
      this.current_input = "";
      this.current_index = this.current_index + 1;
      if (this.current_index > this.question_list.length - 1) {
        this.current_index = 0;
      }
      this.update_current_question_info();
      this.$store.commit("set_short_answer_progress", this.current_index);
    },
    next() {
      if (
        this.get_similarity_from_two_strings(
          this.current_input,
          this.current_question_info.right_answer
        ) >= 0.65 &&
        this.similar(
          this.current_input,
          this.current_question_info.right_answer
        ) >= 0.2
      ) {
